html{
  background-color: #FFCDB2 ;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

#contact{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#name, #email, #message{
  display: flex;
  color: #3c3941;
  font-size: 1.4em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  padding: 1em;
}


#container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

header{
  margin-top: 5px;
}

.home-header{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: #FFCDB2;
  color: #6D6875;
  font-size: 1.5rem;
  padding-top: 1.5rem;
}

.heading-name{
  color: #3c3941;
  font-size: 2.2em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

#portfolio{
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  height: 100%; 
}

.project-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.project-image img {
  width: 100%;
  max-width: 300px;
  height: auto;
}

.project-details {
  margin-top: 10px;
  border: solid 1px #ffffff;
  background-color: #fcc7a4;
}

.project-details h3 {
  margin-bottom: 5px;
}

fab fa-github {
  color: #000000;
}

.project-details .project-description {
  font-size: 14px;
  margin-bottom: 10px;
}

.project-details .project-links {
  display: block;
  margin-bottom: 5px;
}

.project-description {
  font-size: 50px;
  font-family:'Times New Roman', Times, serif;
  color: #25041a;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
}

h2{
  color: #3c3941;
  font-size: 2.2em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
}

img{
  width: 70%;
  height: 70%;
  margin: 1em;
}


.main-name{
  color: #3c3941;
  font-size: 2.2em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
}
.main-title{
  color: #3c3941;
  font-size: 2.2em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
}
 #about{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
 }
 
 #about-paragraph{
  color: #3c3941;
  font-size: 1.4em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  padding: 1em;
 }

 .my-2{
  border-radius: 15px;
  border: 0.2px solid #3c3941;
  padding: 1em;
  margin: 1em;
  width: 50%;
 }


nav{
  position: absolute;
  top: 0;
  width: 100%;
  height: 2.5rem;
  background-color: #282c34;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 1.5rem;
  padding-top: 0.5rem;
}

nav a{
  color: white;
  text-decoration: none;
  list-style-type: none;
  padding: 5px;
}


nav a:hover{
  color: #FFCDB2;
  background-color: #282c34;
}

nav a:active{
  color: #FFCDB2;
  background-color: #282c34;
}

nav a:focus{
  color: #FFCDB2;
  background-color: #282c34;
} 

#resume{
  color: #3c3941;
  font-size: 2.2em!important;
  font-weight: 600!important;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.resume-image {
  width: 100%;
  max-width: 500px;
  height: auto;
}


.resume-download{
  text-decoration: none;
  color: #3c3941;
  font-size: 0.7em!important;
}
.resume-download:hover{
  color: #B5838D;
  text-decoration: underline;
}

.typed-out p{
  overflow: hidden;
  border-right: .15em solid #E5989B ;
  white-space:nowrap;
  font-size: 1.6rem;
  width: 0;
  animation:
  typing 1s steps(20, end) forwards,
  blink .3s infinite;
}

.Typewriter_wrapper{
  color:#6D6875;
  font-size: 2.2em!important;
  font-weight: 600!important;
}

ul{
  display: flex;
  justify-content: right;
  padding: 2;
  margin-top: 2px;
  margin-right: 2px;
  list-style-type: none;
}
.wave{
  position: relative;
  height: 6rem;
  width: 50%;
  margin-bottom: -15px;
  background-color: #FFCDB2;
  animation-name: wave-animation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 20px; /* Adjust the height according to your footer's content */
  background-color: #282c34;
  text-align: center;
  padding: 20px;
}
